.Profilecards{
    overflow: hidden;
}

.card{
    position: relative;
    width: 37vh;
    padding: 20px;
    max-width: 85vw;
    height: 40vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    box-shadow: 0px 18px 53px 0px rgba(0,0,0,0.3);
}

.overflow_hidden{
    overflow: hidden;
}

.card > h3{
    position: absolute;
    bottom: 10px;
    color: #fff;
    font-weight: 400;
    font-size: large;
}

.cardContainer{
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}

.swipe{
    position: absolute;
}